.header-page {
  background: #ffffff;
  color: #ffffff;
  font-size: 18px;
  height: 56px;
  border-bottom: 2px solid #dedede;
  position: relative;
  z-index: 10;
  max-width: 100%;
  padding: 0px 16px;

  .wrapper {
    flex-flow: nowrap;
    align-items: center;
    height: 100%;
    justify-content: space-between;

    .ant-menu-horizontal {
      background: transparent;
      border: 0;
      color: #ffffff;
      padding: 0px 10px;
    }
  }

  &__title {
    flex-shrink: 0;

    h3.ant-typography {
      font-weight: bold;
      font-size: 20px;
      margin-bottom: 0;
    }
  }
  &__search {
    width: 100%;
    max-width: 250px;
  }
}

.menu-item {
  width: 100%;
  height: 100%;
  padding: 2px 8px;

  .menu-item__btn {
    width: 100%;
    height: 100%;
  }
}

@border-radius-base: 3px;